







import Vue from "vue";
import { coordinates, Coordinates } from "../../../common/lib/browser";
import { routeEvents, IRouteSubmit } from "../bus/RouteEvents";

export default Vue.extend({
  data(): { start: Coordinates | null; dest: Coordinates | null } {
    return {
      start: null,
      dest: null
    };
  },
  methods: {
    onRouteSubmit(event: IRouteSubmit) {
      this.start = event.start;
      this.dest = event.dest;
    }
  },
  mounted() {
    routeEvents.listenRouteSubmit(this.onRouteSubmit);
  }
});
